<template>
  <div class="loading"
       :class="{active: show}">
    <span>로딩중...</span>
  </div>
</template>

<script>
export default {
  name: "Loading",
  props: ["show"]
};
</script>

<style lang="scss">
@import "./../assets/style/var";

.loading {
  display: none;
  @include pos();
  background-color: #fff;
  opacity: 0.92;
  z-index: 3;

  &.active {
    display: block;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 14px;
    font-weight: 600;
    transform: translate(-50%, -50%);
  }
}
</style>
