<template>
  <span class="week">
    <ul class="drama"
        v-show="type === 'drama'">
      <li :key="d.value"
          :class="{active: d.value === date}"
          v-for="d in dateForDrama"
          @click="pick(d.value)">{{d.name}}</li>
    </ul>
    <ul class="enter"
        v-show="type === 'enter'">
      <li :key="d.value"
          :class="{active: d.value === date}"
          v-for="d in dateForEnter"
          @click="pick(d.value)">{{d.name}}</li>
    </ul>
  </span>
</template>

<script>
export default {
  name: "Week",
  data: function() {
    return {
      dateForDrama: [
        {
          name: "전 체",
          value: "all"
        },
        {
          name: "월 화",
          value: "mon_tue"
        },
        {
          name: "수 목",
          value: "wed_thu"
        },
        {
          name: "금 토",
          value: "fri_sat"
        },
        {
          name: "일 일",
          value: "sun_sun"
        }
      ],
      dateForEnter: [
        {
          name: "전 체",
          value: "all"
        },
        {
          name: "월",
          value: "mon"
        },
        {
          name: "화",
          value: "tue"
        },
        {
          name: "수",
          value: "wed"
        },
        {
          name: "목",
          value: "thu"
        },
        {
          name: "금",
          value: "fri"
        },
        {
          name: "토",
          value: "sat"
        },
        {
          name: "일",
          value: "sun"
        }
      ]
    };
  },
  props: {
    type: {
      type: String,
      default: "drama"
    },
    pick: {
      type: Function,
      default: function() {}
    },
    date: {
      type: String,
      default: "all"
    }
  }
};
</script>

<style lang="scss">
@import "./../assets/style/var";

.week {
  ul {
    height: $header-height;
  }

  height: inherit;
  line-height: $header-height;

  li {
    float: left;
    text-align: center;
    cursor: pointer;

    &:hover,
    &.active {
      color: #fff;
      background-color: $--color-100;
    }

    &.active {
      cursor: default;
    }
  }

  .drama li {
    width: 82px;
  }

  .enter li {
    width: $header-height;
  }

  li:first-child {
    position: relative;

    &.active::before {
      position: absolute;
      left: -10px;
      top: 20px;
      content: "";
      width: 0;
      height: 0;
      border-width: 10px 10px 10px 0;
      border-style: solid;
      border-color: transparent $--color-100 transparent transparent;
    }
  }
}
</style>
