<template>
  <a class="close"
     @click="handleClose">
    <i>화면에 변두리를 클릭하셔도 꺼집니다</i>
  </a>
</template>

<script>
import bus from "@/utils/bus";

export default {
  name: "Close",
  methods: {
    handleClose() {
      bus.$emit("close");
    }
  }
};
</script>

<style lang="scss">
@import "./../assets/style/var";

.close {
  position: absolute;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.close::before,
.close::after {
  content: "";
  position: absolute;
  right: 15px;
  width: 1px;
  height: 30px;
  background-color: #898d9d;
}

.close::before {
  transform: rotate(45deg);
}

.close::after {
  transform: rotate(-45deg);
}

.close:hover::before,
.close:hover::after {
  background-color: #2c3e50;
}

.close i {
  position: absolute;
  width: 238px;
  padding: 4px 0;
  margin-left: -248px;
  margin-top: 3px;
  border-radius: 2px;
  font-size: 12px;
  color: #fff;
  background-color: $--color-300;
  text-align: center;
  word-break: keep-all;
  opacity: 0.7;

  &::after {
    position: absolute;
    right: -5px;
    top: 2px;
    content: "";
    width: 0;
    height: 0;
    border-width: 5px 0 5px 5px;
    border-style: solid;
    border-color: transparent transparent transparent $--color-300;
    margin: 5px auto;
  }
}
</style>
