import Vue from "vue";
import axios from "axios";
import App from "./App.vue";
import router from "./router";
import state from "./state";
import "@/assets/style/normal.css";

Vue.prototype.$http = axios;
Vue.config.productionTip = false;

new Vue({
  data: { state },
  router,
  render: h => h(App)
}).$mount("#app");
