import Vue from "vue";
import Router from "vue-router";
import NotFound from "./views/404.vue";
import Drama from "./views/Drama.vue";
import Enter from "./views/Enter.vue";

Vue.use(Router);

const router = new Router({
  mode: "hash",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/drama",
      name: "drama",
      component: Drama
    },
    {
      path: "/enter",
      name: "enter",
      component: Enter
    },
    {
      path: "/",
      name: "default",
      redirect: "/drama"
    },
    {
      path: "*",
      name: "404",
      component: NotFound
    }
  ]
});

export default router;
