<template>
  <div class="detail"
       @click="handleClose">
    <close />
    <div class="content">
      <span class="info">
        <div class="prop">
          <img :src="item.img"
               :alt="item.title" />
          <div>
            <h2>{{item.title}}</h2>
            <i>{{week}}</i>
            <p>{{item.actor}}</p>
            <i>{{item.time}}</i>
          </div>
        </div>
        <ul>
          <li :key="index"
              v-for="(ep, index) in item.episodes"
              @click="handlePlay(index)">
            <i :class="{active: currentEp.text == ep.text}">
              {{ep.text.replace(/^\s+|\s+$/gm,'')}}
            </i>
          </li>
        </ul>
      </span>
      <span class="video">
        <iframe width="100%"
                height="100%"
                :src="currentEp.src"
                frameborder="0"
                allowfullscreen="true"
                border="0"
                marginwidth="0"
                marginheight="0"
                scrolling="no"></iframe>
      </span>
    </div>
  </div>
</template>

<script>
import Close from "@/components/Close";
import bus from "@/utils/bus";

let bodyEl;
const BASE_URL = process.env.BASE_URL;

export default {
  name: "Detail",
  components: {
    Close
  },
  props: {
    item: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  data: function() {
    return {
      epList: [],
      currentEp: {}
    };
  },
  computed: {
    week() {
      return {
        "1_2": "월/화",
        "3_4": "수/목",
        "5_6": "금/토",
        "7_7": "일/일",
        "1": "월",
        "2": "화",
        "3": "수",
        "4": "목",
        "5": "금",
        "6": "토",
        "7": "일"
      }[this.item.week];
    }
  },
  methods: {
    init() {
      bus.$on("close", () => {
        this.$root.state.item = null;
      });

      this.$http
        .get(`${BASE_URL}data/${this.item.type}/${this.item.id}/epi.json`)
        .then(res => {
          if (res.status === 200) {
            this.epList = res.data;
            this.currentEp = this.epList.length ? this.epList[0] : {};
          }
        });
    },
    handleClose($event) {
      if ($event.target.className === "detail") {
        this.$root.state.item = null;
      }
    },
    handlePlay(index) {
      this.currentEp = this.epList[index];
    }
  },
  created: function() {
    this.init();
  },
  mounted: function() {
    bodyEl = document.querySelector("body");
    bodyEl.style.overflow = "hidden";
  },
  beforeDestroy: function() {
    bus.$off("close");
    bodyEl.style.overflow = "auto";
  }
};
</script>

<style lang="scss">
@import "./../assets/style/var";

.detail {
  @include pos();
  background-color: $--color-500;
  opacity: 0.96;
  z-index: 2;
}

.close {
  top: 15px;
  right: $space;
}

.content {
  @include pos(
    absolute,
    $header-height,
    $header-height,
    $header-height,
    $header-height
  );
  background-color: #fff;
  box-shadow: 0 2px 4px 0 $--color-400;
}

.video {
  float: right;
  width: 70%;
  height: 100%; // 467px
  background-color: $--color-200;
  overflow: hidden;
}

.info {
  display: inline-block;
  width: 30%;
  height: 100%;
  padding: $space $space 10px;
  overflow: auto;
}

.prop {
  display: inline-table;
  width: 100%;
  height: 180px;
  margin-bottom: $space;
  border-bottom: 1px solid $--color-500;

  img {
    float: left;
    width: 40%;
    margin-bottom: $space;
    border: 1px solid $--color-400;
    background-color: $--color-500;
  }

  div {
    width: 60%;
    padding-left: $space;
    padding-bottom: $space;
    margin-left: 40%;
  }

  h2 {
    margin: 0;
  }

  i {
    display: block;
    line-height: 17px;
    padding: 2px 0;
    border: 1px solid $--color-400;
    border-radius: 2px;
    font-size: 12px;
    text-align: center;
    color: $--color-300;
    background-color: $--color-500;
  }

  h2 ~ i {
    width: 45px;
    margin-top: 5px;
  }

  p ~ i {
    width: 85px;
  }

  h2,
  p {
    text-align: justify;
  }
}

.info ul {
  li {
    display: inline-block;
    width: 50%;
    margin-bottom: 10px;

    &:nth-child(odd) i {
      margin-right: 5px;
    }

    &:nth-child(even) i {
      margin-left: 5px;
    }
  }

  i {
    display: block;
    height: 35px;
    line-height: 33px;
    padding: 0 10px;
    border: 1px solid;
    border-radius: 20px;
    text-align: center;
    color: $--color-100;
    cursor: pointer;
    transition: all 100ms ease-in-out;
    @include ell();

    &:hover,
    &.active {
      color: #fff;
      background-color: $--color-100;
    }
  }
}
</style>
