<template>
  <div class="enter wrapper"
       ref="wrapper">
    <card :key="item.id"
          v-for="item in list"
          :title="item.title"
          :img="item.img"
          :time="item.time"
          :ep="item.episodes.length && item.episodes[0]"
          @click.native="handleSelect(item)" />
  </div>
</template>

<script>
import Card from "@/components/Card.vue";
import json1 from "@/assets/data/enter/gogumatv.com-1-23.json";
import json2 from "@/assets/data/enter/gogumatv.com-2-24.json";
import json3 from "@/assets/data/enter/gogumatv.com-3-25.json";
import json4 from "@/assets/data/enter/gogumatv.com-4-26.json";
import json5 from "@/assets/data/enter/gogumatv.com-5-27.json";
import json6 from "@/assets/data/enter/gogumatv.com-6-28.json";
import json7 from "@/assets/data/enter/gogumatv.com-7-29.json";

const MAP = {
  all: [
    ...json1,
    ...json2,
    ...json3,
    ...json4,
    ...json5,
    ...json6,
    ...json7
  ].sort((a, b) => (a.time < b.time ? 1 : -1)),
  mon: json1,
  tue: json2,
  wed: json3,
  thu: json4,
  fri: json5,
  sat: json6,
  sun: json7
};

const CARD_W = 185;
const SPACE = 20;

export default {
  name: "Enter",
  components: {
    Card
  },
  data: function() {
    return {
      list: []
    };
  },
  watch: {
    "$root.state.dateForEnter": function(newVal) {
      this.list = MAP[newVal].filter(item => item.episodes.length !== 0);

      this.$nextTick(() => {
        this.layout();
      });
    }
  },
  methods: {
    init() {
      this.list = MAP["all"].filter(item => item.episodes.length !== 0);
    },
    layout() {
      const wrapper = this.$refs.wrapper;
      const wrapperW = wrapper.clientWidth - SPACE * 2;
      const rowCount = Math.floor(wrapperW / CARD_W);
      const marginRight = (wrapperW - rowCount * CARD_W) / (rowCount - 1);

      Array.from(wrapper.children).forEach(function(card, index) {
        if (index === 0 || (index + 1) % rowCount !== 0) {
          card.style.marginRight = marginRight + "px";
        } else {
          card.style.marginRight = "0px";
        }
      });
    },
    handleSelect(item) {
      this.$root.state.item = item;
    }
  },
  beforeRouteEnter: function(to, from, next) {
    next(function(vm) {
      vm.list = MAP[vm.$root.state.dateForEnter || "all"].filter(
        item => item.episodes.length !== 0
      );

      vm.$nextTick(() => {
        vm.layout();
      });
    });
  },
  created: function() {
    this.init();
  },
  mounted: function() {
    this.layout();
    this.$root.state.isLoading = false;
  }
};
</script>

<style lang="scss">
@import "./../assets/style/common";
</style>
