<template>
  <div class="drama wrapper"
       ref="wrapper">
    <card :key="item.id"
          v-for="item in list"
          :title="item.title"
          :img="item.img"
          :time="item.time"
          :ep="item.episodes.length && item.episodes[0]"
          @click.native="handleSelect(item)" />
  </div>
</template>

<script>
import Card from "@/components/Card.vue";
import json12 from "@/assets/data/drama/gogumatv.com-1_2-13.json";
import json34 from "@/assets/data/drama/gogumatv.com-3_4-14.json";
import json56 from "@/assets/data/drama/gogumatv.com-5_6-15.json";
import json77 from "@/assets/data/drama/gogumatv.com-7_7-21.json";

const MAP = {
  all: [...json12, ...json34, ...json56, ...json77].sort((a, b) =>
    a.time < b.time ? 1 : -1
  ),
  mon_tue: json12,
  wed_thu: json34,
  fri_sat: json56,
  sun_sun: json77
};

const CARD_W = 185;
const SPACE = 20;

export default {
  name: "Drama",
  components: {
    Card
  },
  data: function() {
    return {
      list: []
    };
  },
  watch: {
    "$root.state.dateForDrama": function(newVal) {
      this.list = MAP[newVal].filter(item => item.episodes.length !== 0);

      this.$nextTick(() => {
        this.layout();
      });
    }
  },
  methods: {
    init() {
      this.list = MAP["all"].filter(item => item.episodes.length !== 0);
    },
    layout() {
      const wrapper = this.$refs.wrapper;
      const wrapperW = wrapper.clientWidth - SPACE * 2;
      const rowCount = Math.floor(wrapperW / CARD_W);
      const marginRight = (wrapperW - rowCount * CARD_W) / (rowCount - 1);

      Array.from(wrapper.children).forEach(function(card, index) {
        if (index === 0 || (index + 1) % rowCount !== 0) {
          card.style.marginRight = marginRight + "px";
        } else {
          card.style.marginRight = "0px";
        }
      });
    },
    handleSelect(item) {
      this.$root.state.item = item;
    }
  },
  beforeRouteEnter: function(to, from, next) {
    next(function(vm) {
      vm.list = MAP[vm.$root.state.dateForDrama || "all"].filter(
        item => item.episodes.length !== 0
      );

      vm.$nextTick(() => {
        vm.layout();
      });
    });
  },
  created: function() {
    this.init();
  },
  mounted: function() {
    this.layout();
    this.$root.state.isLoading = false;
  }
};
</script>

<style lang="scss">
@import "./../assets/style/common";
</style>
