<template>
  <div id="app">
    <header>
      <h3 class="logo">
        <img src="./assets/image/logo.png"
             title="www.hi123.site" />Hi123.site
      </h3>
      <nav>
        <router-link to="/drama">드라마</router-link>
        <router-link to="/enter">예능</router-link>
      </nav>
      <week :type="$root.state.channel"
            :pick="pick"
            :date="date" />
    </header>
    <main>
      <router-view />
      <detail v-if="$root.state.item"
              :item="$root.state.item" />
    </main>
    <loading :show="$root.state.isLoading" />
  </div>
</template>

<script>
import Week from "@/components/Week.vue";
import Detail from "@/components/Detail.vue";
import Loading from "@/components/Loading.vue";

export default {
  name: "App",
  components: {
    Week,
    Detail,
    Loading
  },
  data: function() {
    return {
      date: "all"
    };
  },
  watch: {
    "$route.name": function(newVal) {
      this.$root.state.isLoading = true;
      this.$root.state.channel = newVal;
      if (newVal === "drama")
        this.date = this.$root.state.dateForDrama || "all";
      if (newVal === "enter")
        this.date = this.$root.state.dateForEnter || "all";
    }
  },
  methods: {
    pick(date) {
      this.date = date;
      if (this.$root.state.channel === "drama")
        this.$root.state.dateForDrama = date;
      if (this.$root.state.channel === "enter")
        this.$root.state.dateForEnter = date;
    }
  }
};
</script>

